import { useState, useEffect } from 'react';
import React,{ Component , Fragment } from 'react';
function TodoList() {
    const [inputValue,inputChange] = useState('')
    const [data, listchange] = useState({list:['qwe','wqe']})

  
    useEffect(() => {
      document.title = `${inputValue}`
    }, [inputValue])
  
    /* useEffect(() => {
      
    }, [name]) */
  
    return (
      <Fragment>
        {/* <p>You click {count} times</p>
        <button onClick={e => setCount(count => count + 1)}>Click me</button> */}
            <input onChange={e => inputChange(e.target.value)} />
            <button onClick={() => listchange(data.list = [...data.list,inputValue])}>Submit</button>
            <ul>
                {data.list.map((value,index) => {
                return (
                    <div key={index}>
                    <li>{value}</li>
                    </div>
                )
                })}
            </ul>
           {/*  <div>{data.list}</div> */}
      </Fragment>
    )
  }
export default TodoList;
